import { Component } from "react";
import { Modal, Button, InputGroup, Form, Image, OverlayTrigger, Tooltip } from "react-bootstrap";
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";
import ImageUploader from "../../../../../CommonComponents/ImageUploader";
import { ADD_UGC_TEMPLATE } from "../../../../../../actions/moderationActions";
import { connect } from "react-redux";

class CreateTemplate extends Component {
  state = { brandFilename: this.props.editData.client_logo, selectedBrand: this.props.editData.client_logo_name, selectedBrandFile: this.props.editData.client_logo, tncFilename: this.props.editData.custom_terms, selectedtnc: this.props.editData.custom_terms_name, selectedtncFile: this.props.editData.custom_terms, selectedTemplate: null, acceptTnc: false, logoError: false, tncError: false, templateName: this.props.editData.name, custom_msg: this.props.editData.custom_message, tncerrorMsg: false, nameerrorMsg: false, msgerrorMsg: false, };

  setBrandLogo = (imgUrl, mediaData) => this.setState({ selectedBrandFile: imgUrl, brandFilename: mediaData.fileName, selectedBrand: mediaData.fileName })
  setTnc = (imgUrl, mediaData) => this.setState({ tncFilename: mediaData.fileName, selectedtnc: mediaData.fileName, selectedtncFile: imgUrl, })

  removeBrandLogo = () => this.setState({ selectedBrand: "", selectedBrandFile: null, brandFilename: null })
  removeTnc = () => this.setState({ tncFilename: null, selectedtnc: "", selectedtncFile: null, })


  onSaveTemplate = () => {
    const { tncFilename, brandFilename, selectedtncFile, templateName, selectedBrandFile, custom_msg } = this.state;

    if (!custom_msg || !templateName || !tncFilename || !brandFilename) {
      if (!custom_msg) this.setState({ msgerrorMsg: true })
      if (!templateName) this.setState({ nameerrorMsg: true })
      if (!tncFilename) this.setState({ tncerrorMsg: true })
      if (!brandFilename) this.setState({ logoError: true })
    }
    else {
      var obj = { ownerId: localStorage.getItem('ownerId'), name: templateName, custom_message: custom_msg, custom_terms: selectedtncFile, custom_terms_name: tncFilename, client_logo: selectedBrandFile, client_logo_name: brandFilename, ugcterms: '' }
      if (this.props.edit) obj = { ...obj, id: this.props.editData.id }
      this.props.ADD_UGC_TEMPLATE(obj, this.props.onAddNewTemplate)
    }
  }



  render() {
    const { show, onHide, edit } = this.props;
    const { tncFilename, logoError, selectedtncFile, selectedBrandFile, templateName, custom_msg, tncerrorMsg, nameerrorMsg, msgerrorMsg } = this.state;


    return (
      <>
        <Modal size="lg" show={show} onHide={onHide} centered>
          <Modal.Header closeButton>
            <Modal.Title>{edit ? 'Edit' : `Create New`} Template</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form.Group className="mb-3">
              <Form.Label htmlFor="t_name">Template Name <span className="text-danger">*</span> </Form.Label>
              <Form.Control placeholder="enter your template name" autoComplete="off" id="t_name" className={`${nameerrorMsg ? `is-invalid` : ``}`} value={templateName} onChange={(e) => this.setState({ templateName: e.target.value, nameerrorMsg: false })} maxLength="255" />
              <span id="t_nameerror" className="invalid-feedback" >{nameerrorMsg ? "Name is required" : ''}</span>
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label htmlFor="t_msg">Custom Message <span className="text-danger">*</span> </Form.Label>
              <Form.Control as={'textarea'} className={`${msgerrorMsg ? `is-invalid` : ``}`} id="t_msg" maxLength="255" cols="30" rows="6" value={custom_msg} onChange={(e) => this.setState({ custom_msg: e.target.value, msgerrorMsg: false })} />
              <span id="t_msgerror" className="invalid-feedback" >{msgerrorMsg ? "Custom message is required" : ''}</span>
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label htmlFor="t_logo">Brand Logo <span className="text-danger">*</span></Form.Label>
              {!selectedBrandFile ? <ImageUploader uploadPath={'media/tmpimages/'} uploadAgents={["GoogleDrive", "Url"]} allowedFile={['image/*']} onImgUpload={this.setBrandLogo} >
                <div className={`p-5 border border-dashed rounded-2 cursor-pointer ${logoError ? `is-invalid border-danger` : ``}`} style={{ maxWidth: 300 }}>
                  <p className="mb-0 text-center text-muted fw-medium">Upload Logo</p>
                </div> </ImageUploader> :
                <div className="d-flex align-items-center">
                  <div className="me-3 me-xxl-4">
                    <div className="symbol h-90px w-90px position-relative rounded-2 overflow-hidden">
                      <Image src={selectedBrandFile} alt={''} height={90} width={90} className="object-center-cover" load="lazy" fluid />
                    </div>
                  </div>
                  <div className="d-flex align-items-center flex-wrap">
                    <div className="d-flex flex-column align-items-center flex-nowrap mb-0">
                      <OverlayTrigger placement="top" overlay={<Tooltip>Remove</Tooltip>}>
                        <Button variant="icon" className="btn-active-danger" onClick={this.removeBrandLogo}> <FontAwesomeIcon type={'solid'} icon={'trash-can'} /> </Button>
                      </OverlayTrigger>
                    </div>
                  </div>
                </div>
              }
              <span id="t_logoerror" className="invalid-feedback">Brand Logo is required</span>
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label htmlFor="t_msg">Terms and Conditions<span className="text-danger">*</span></Form.Label>
              <InputGroup className={`${tncerrorMsg ? `is-invalid` : ``}`}>
                <div className="form-control form-control-lg">
                  {selectedtncFile ?
                    <div className="overflow-hidden d-flex align-items-center">
                      <span className="text-ellipsis fs-8 p-1 rounded-1">{tncFilename}</span>
                      <span className="cursor-pointer p-1 mx-1 rounded-1 f-center text-center text-danger" onClick={this.removeTnc}>
                        <FontAwesomeIcon type={'solid'} icon={'xmark'} />
                      </span>
                    </div> : ''}
                </div>
                <ImageUploader uploadPath={'media/tmpimages/'} uploadAgents={["GoogleDrive", "Url"]} allowedFile={['application/pdf/*']} onImgUpload={this.setTnc}>
                  <InputGroup.Text className="cursor-pointer h-100"  >Attach File</InputGroup.Text>
                </ImageUploader>
              </InputGroup>
              <span id="tcError" className="invalid-feedback" >{tncerrorMsg ? "Terms & Conditions is required" : ''}</span>
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button variant='secondary' className='ms-3 btn-min-w' onClick={onHide}>Cancel</Button>
            <Button className='ms-3 btn-min-w' onClick={this.onSaveTemplate}>Save Template</Button>
          </Modal.Footer>

        </Modal>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    // ugcData: state.moderation.ugcData,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    ADD_UGC_TEMPLATE: (data, callback) => dispatch(ADD_UGC_TEMPLATE(data, callback))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateTemplate);
