import React, { Component } from 'react'
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import '@splidejs/splide/css';
import { Button, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../../utils';
import ShowMoreContent from '../../../../../CommonComponents/ShowMoreContent';
import ImageType from '../../../../../CommonComponents/MediaType/ImageType';
import Author from '../../../../../CommonComponents/Author/Author';
import { baseUrl, FontAwesomeIcon } from '../../../../../CommonComponents/AssetsHelper';
import ReactHtmlParser from 'react-html-parser';
import ModalMedia from '../../ContentModal/Elements/ModalMedia';



export default class CardPreview extends Component {
  constructor(props) {
    super(props);
    this.mainRef = React.createRef();
  }

  handleThumbs = (id) => {
    if (this.mainRef.current) {
      this.mainRef.current.go(id);
    }
  };

  render() {

    const {ugcData, handleAllImageError} = this.props

    const mainOptions = {
      type      : 'loop',
      perPage   : 1,
      perMove   : 1,
      pagination: false,
      navigation: false,
      arrows    : ugcData?.data?.length > 1
    };

    return <div className='mx-auto w-100' style={{maxWidth:450}}>
              <Splide hasTrack={ false } options={mainOptions}>
                <SplideTrack>
                {ugcData.data && ugcData.data.length ? ugcData.data.map((item,index)=>{ 
                  return <SplideSlide key={index}>
                      <div className={'d-flex flex-column w-100'}>
                        {/* <ImageType mediaClass={'position-relative overflow-hidden w-100'} size={100} height={64} width={64} url={item.postFile} alt={""} isCover={true} /> */}
                        <div style={{maxHeight:450}}>
                        <ModalMedia modalData={item} handleAllImageError={handleAllImageError} />
                        </div>
                        <div className='p-3'>
                          <div className="d-flex align-items-center mb-2">
                              <div className="d-flex align-items-start me-2 flex-grow-1 overflow-hidden text-decoration-none">
                                  <Author className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                      imageSize={40}
                                      symbolClass={'symbol-label fw-bolder fs-5'}
                                      name={item.name} profileUrl={item.picture} networkId={item.networkId} />
                                  <div className="pe-2 w-100 overflow-hidden">
                                      <p className=" mb-0"><strong className="text-break text-ellipsis w-100 d-block">{item.name}</strong></p>
                                      <p className="mb-1 text-gray-500 text-break fs-9">@{item.username}</p>
                                  </div>
                              </div>  
                              <span className="d-flex align-items-center h-20px w-20px flex-shrink-0">
                                  <OverlayTrigger placement="top" overlay={<Tooltip>{GET_NETWORK_NAME(item.networkId)}</Tooltip>}>
                                      <FontAwesomeIcon type='brands' icon={GET_NETWORK_ICON(item.networkId)} iconClass={'fs-3'} />
                                  </OverlayTrigger>
                              </span>
                          </div>
                        </div>
                        <div>{ReactHtmlParser(item.postcontent.slice(0, 150))} {item.postcontent?.length > 150 ? '...' : '' }</div>
                      </div>
                  </SplideSlide>
                    }): <SplideSlide>
                    <div className={'d-flex flex-column w-100'}>
                      <div style={{maxHeight:450}}>
                      <div class="pb-0"><div class="sk_img rounded-0 w-100 flex-shrink-0 overflow-hidden" style={{paddingBottom: '100%'}}></div></div>
                      </div>
                      <div>
                      <div class="d-flex align-items-center p-3"><div class="d-flex align-items-center me-2 flex-grow-1 overflow-hidden"><div class="sk_img symbol symbol-36 rounded-circle flex-shrink-0 me-2 overflow-hidden"></div><div class="overflow-hidden w-100" style={{maxWidth: '130px'}}><p class="sk_line sk_line_100 sk_line_height_10 mb-2"></p><span class="sk_line sk_line_50 sk_line_height_8 mb-0 d-block"></span></div></div><span class="d-flex align-items-end flex-column flex-shrink-0" style={{width: '100px'}}><div class="sk_img symbol symbol-28 rounded-1 flex-shrink-0 me-2 overflow-hidden"></div></span></div>
                      <div className='pt-4'><p class="sk_line sk_line_100 sk_line_height_10 mb-2"></p> <p class="sk_line sk_line_100 sk_line_height_10 mb-2"></p> <p class="sk_line sk_line_100 sk_line_height_10 mb-2"></p></div>
                      
                      </div>
                      <div></div>
                    </div>
                </SplideSlide>}
                </SplideTrack>

                {/* <div className="splide__arrows" style={{display:ugcData?.data?.length > 1 ? 'block' : 'none'}}>
                  <Button variant='secondary' className="splide__arrow splide__arrow--prev start-0">
                        <FontAwesomeIcon type={'solid'} icon={'arrow-left'}/>
                  </Button>
                  <Button variant='secondary' className="splide__arrow splide__arrow--next end-0">
                      <FontAwesomeIcon type={'solid'} icon={'arrow-right'}/>
                  </Button>
                </div> */}
              </Splide>
            </div>

  }
}
