import { useState } from "react";
import { useEffect } from "react";
import { Modal, Button, Nav } from "react-bootstrap";
import { ADD_UGC_TEMPLATE, SEND_UGC } from "../../../../../actions/moderationActions";
import CardPreview from "./Elements/CardPreview";
import CopyUrl from "./Elements/CopyUrl";
import GeneralRequest from "./Elements/GeneralRequest";
import SocialAccounts from "./Elements/SocialAccounts";
import './rights.scss'
import ChooseMethod from "./Elements/ChooseMethod";
import { useDispatch } from "react-redux";
// import { TAGGSHOP_SOCIAL_ACCOUNT_DATA } from "../../../../../actions/sceneActions";
import { FontAwesomeIcon } from "../../../../CommonComponents/AssetsHelper";
import { TOAST_ERROR } from "../../../../../utils";
import ErrorFound from "../../../../CommonComponents/ErrorFound";
import { Link } from "react-router-dom";


const UgcModal = (props) => {
  const { hideUgc, ugcData } = props
  const [ugcTabKey, setUgcTabKey] = useState('general')
  const [custom_msg, setCustom_msg] = useState("")
  const [selectedTemplate, setSelectedTemplate] = useState(localStorage.getItem('selectedTemplate') || '')
  const [acceptTnc, setAcceptTnc] = useState(false)
  const [tncError, setTncError] = useState(false)
  const [step, setStep] = useState(4)
  const [isAccount, setIsAccount] = useState('')
  const [userName, setUserName] = useState('')
  const [brand_link, setBrand_link] = useState('')
  const [termslink, setTermslink] = useState('')
  const [termstype, setTermstype] = useState('')
  const [requestMode, setRequestMode] = useState()
  const [isRequesting, setIsRequesting] = useState(false)
  const dispatch = useDispatch()


  useEffect(() => {
    // dispatch(TAGGSHOP_SOCIAL_ACCOUNT_DATA())
  }, [])

  const sendRequest = () => {
    if (isAccount) {
      setStep(3)
      onRaiseRequest()
    }
  }

  const getPostIds = () => {
    const { data } = ugcData
    let postIds = []
    if (data?.length > 0) {
      data?.map((item, index) => {
        postIds.push(item.referenceId)
      })
    }
    return postIds.join(',')
  }


  const onRaiseRequest = (hash) => {
    const { template, data, unique_code, bitlyurl } = ugcData
    setIsRequesting(true)
    const index = template?.findIndex(t => t.id == selectedTemplate)
    let tempSelected = template[index]
    if (!tempSelected) tempSelected = template[0]
    var obj = {
      custom_message: custom_msg,
      feedId: data[0].feedId,
      networkId: data[0].networkId,
      selectedImage: data[0].postFile,
      selectedVideo: '',
      selectedUsername: data[0].username,
      handle: userName,
      postId: data.length > 1 ? getPostIds() : data[0].referenceId,
      brand_link: brand_link ? brand_link : tempSelected?.client_logo,
      termslink: termslink ? termslink : tempSelected?.custom_terms,
      termstype: termstype ? termstype : tempSelected?.custom_terms_name,
      bitlylink: bitlyurl,
      unique_code: unique_code,
      rightsVia: requestMode,
      hashtag: hash,
      networkPostId: data[0].postId,
      wallId: localStorage.getItem('wallId'),
      client: localStorage.getItem('ownerId')
    }
    SEND_UGC(obj, () => setIsRequesting(false))
  }



  const onUpdateTemplate = () => {
    const { template } = ugcData
    const index = template?.findIndex(t => t.id == selectedTemplate)
    let tempSelected = template[index]
    if (!tempSelected) tempSelected = template[0]
    var obj = {
      client_logo: brand_link ? brand_link : tempSelected?.client_logo,
      custom_terms: termslink ? termslink : tempSelected?.custom_terms,
      custom_terms_name: termstype ? termstype : tempSelected?.custom_terms_name,
      custom_message: custom_msg,
      name: tempSelected?.name,
      owner: localStorage.getItem('ownerId'),
      id: tempSelected?.id
    }
    if (![1, 2, 3].includes(tempSelected?.id)) {
      if (!custom_msg) {
        TOAST_ERROR('Message field is required')
      }
      else {
        dispatch(ADD_UGC_TEMPLATE(obj, ''))
      }
    }
    else { }
  }

  return <Modal size={'full'} show={true} className="assets_modal p-0" backdrop={false}>
    <Modal.Header className="justify-content-between py-0 ps-0">
      <div className='d-flex align-items-center'>
        <Link to={``} className="btn me-3 border-end border-0 py-3 rounded-0" onClick={() => hideUgc()}>
          <FontAwesomeIcon icon={'chevron-left p-0'} />
        </Link>
        <span className='text-dark fw-medium'>UGC Rights</span>
      </div>
      <div className='d-flex align-items-center h-100'>
        <Button variant='link' className="me-0 p-0 border-0 moderation_back" onClick={() => hideUgc()}>
          <FontAwesomeIcon type={'regular'} icon={'xmark'} iconClass={'fs-5 me-1'} />
        </Button>
      </div>
    </Modal.Header>
    <Modal.Body className="p-0 d-flex border-0 flex-lg-row flex-column">
      <div className="rights_modal_inner f-center flex-column flex-grow-1 p-lg-4 p-xxl-5 position-relative mb-lg-0 overflow-auto">
        <CardPreview ugcData={ugcData} />
      </div>
      <div className="rights_modal_sidebar position-relative">

        {step != 4 ? <div className="ugc-wizard-nav">
          <Nav variant="nav" className="bg-gray-200 justify-content-between row gx-0">
            <Nav.Item className="col-4">
              <Nav.Link className={`py-3 py-xl-4 f-center ${[1, 2, 3].includes(step) ? `active` : ``}`} onClick={() => setStep(1)}>
                <span className={`d-flex flex-shrink-0 me-1 ${[1, 2, 3].includes(step) ? 'text-success' : 'text-muted'}`}>
                  <FontAwesomeIcon type={[1, 2, 3].includes(step) ? 'solid' : 'regular'} icon="circle-check" />
                </span>
                <span className="fw-bold text-nowrap text-body">General Request</span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className="col-4">
              <Nav.Link className={`py-3 py-xl-4 f-center ${step == 2 || step == 3 ? `active` : `disabled`}`} onClick={() => acceptTnc ? setStep(2) : ''}>
                <span className={`d-flex flex-shrink-0 me-1 ${((acceptTnc && requestMode == 0) || requestMode == 1) && [2, 3].includes(step) ? 'text-success' : 'text-muted'}`}>
                  <FontAwesomeIcon type={((acceptTnc && requestMode == 0) || requestMode == 1) && [2, 3].includes(step) ? 'solid' : 'regular'} icon="circle-check" />
                </span>
                <span className="fw-bold text-nowrap text-body">Social Account</span>
              </Nav.Link>
            </Nav.Item>
            <Nav.Item className="col-4">
              <Nav.Link className={`py-3 py-xl-4 f-center ${step == 3 ? `active` : `disabled`}`} eventKey="ugc-copyUrl">
                <span className={`d-flex flex-shrink-0 me-1 ${[3].includes(step) ? 'text-success' : 'text-muted'}`}>
                  <FontAwesomeIcon type={[3].includes(step) ? 'solid' : 'regular'} icon="circle-check" />
                </span>
                <span className="fw-bold text-nowrap text-body">Preview</span>
              </Nav.Link>
            </Nav.Item>
          </Nav>
        </div>
          : <></>}



        {ugcData && step == 4 ? <ErrorFound><ChooseMethod ugcData={ugcData} requestMode={requestMode} setStep={setStep} setRequestMode={setRequestMode} hideUgc={hideUgc} /></ErrorFound> : null}

        {ugcData && step == 1 ? <ErrorFound><GeneralRequest ugcData={ugcData} requestMode={requestMode} setStep={setStep} setAcceptTnc={setAcceptTnc} acceptTnc={acceptTnc} tncError={tncError} setTncError={setTncError} selectedTemplate={selectedTemplate} setSelectedTemplate={setSelectedTemplate} setCustom_msg={setCustom_msg} custom_msg={custom_msg} setBrand_link={setBrand_link} brand_link={brand_link} setTermslink={setTermslink} setTermstype={setTermstype} termstype={termstype} hideUgc={hideUgc} onUpdateTemplate={onUpdateTemplate} /></ErrorFound> : null}

        {((acceptTnc && requestMode == 0) || requestMode == 1) && step == 2 ? <ErrorFound><SocialAccounts setStep={setStep} setUserName={setUserName} setIsAccount={setIsAccount} networkId={ugcData?.data[0].networkId != 18 ? ugcData?.data[0].networkId : 2} /></ErrorFound> : ""}

        {custom_msg && ugcData && step == 3 ? <ErrorFound><CopyUrl requestMode={requestMode} onRaiseRequest={onRaiseRequest} setUserName={setUserName} networkId={ugcData?.data[0].networkId} setUgcTabKey={setUgcTabKey} setStep={setStep} ugcData={ugcData} custom_msg={custom_msg} isRequesting={isRequesting} /></ErrorFound> : null}

      </div>

    </Modal.Body>
  </Modal>
};

export default (UgcModal);
