import { Component } from "react";  
import { connect } from "react-redux";
import { Button, Form} from "react-bootstrap";
import { TOAST_ERROR } from "../../../../../../utils";
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";

class ChooseMethod extends Component {

    state = {
    };

    componentDidMount() {
        
    }

    onNextStep = () => {
        const { requestMode } = this.props
        if (requestMode != null) {
            this.props.setStep(1)
        }
        else TOAST_ERROR('Please select the mode for Right Request')
    }


    render() {
        const { setRequestMode, requestMode, ugcData } = this.props

        return (
                <>
                    <div className="p-4 py-3 rights_modal_aside_wrap rights_no_wizard">
                        <h3 className="mb-1 h5">Select the mode for Right Request</h3>
                        <p className="text-muted">Socialwalls offers two distinct modes to request rights. Comment Request and DM Request. </p>

                        <div>
                            <div value={'viaComments'} className={`cursor-pointer mb-4 f-between border p-3 rounded-1 ${requestMode == 1 ? 'border-success' : ''}`} onClick={() => setRequestMode(1)}>
                                <div className="d-flex align-items-center me-3 fw-normal">
                                    <FontAwesomeIcon icon={'comment'} iconClass={'fs-2'}/>
                                    <span className="text-start ps-3">
                                        <p className="h6 mb-0">Comment Request</p>
                                        <span className="fs-9">Request creators consent as a comment on their post.</span>
                                    </span>
                                </div>
                                <Form.Check.Input type={'radio'} name={'rights-via'} className="d-none" />
                                <span className={`w-20px fs-6 ${requestMode == 1 ? 'text-success' : 'text-muted'}`}>
                                    <FontAwesomeIcon type={requestMode == 1 ? 'solid' : ''} icon={'check-circle'} />
                                </span>
                            </div>
                            <div className={`cursor-pointer mb-4 f-between border p-3 rounded-1 ${requestMode == 0 ? 'border-success' : ''}`} onClick={() => setRequestMode(0)}>
                                <div className="d-flex align-items-center me-3 fw-normal">
                                    <FontAwesomeIcon icon={'message'} iconClass={'fs-2'}/>
                                    <span className="text-start ps-3">
                                        <p className="h6 mb-0">DM Request</p>
                                        <span className="fs-9">Send a Rights request directly into the DM of the creators!</span>
                                    </span>
                                </div>
                                <Form.Check.Input type={'radio'} name={'rights-via'} className="d-none" />
                                <span className={`w-20px fs-6 ${requestMode == 0 ? 'text-success' : 'text-muted'}`}>
                                    <FontAwesomeIcon type={requestMode == 0 ? 'solid' : ''} icon={'check-circle'} />
                                </span>
                            </div>
                        </div>
                    </div>

                    <div className="d-flex justify-content-end p-4 border-top">
                        <Button variant="secondary" className="me-2" onClick={() => this.props.hideUgc()}>Cancel</Button>
                        <Button onClick={() => this.onNextStep()} style={{pointerEvents:ugcData?.data?.length ? '':'none'}}>Next Step</Button>
                    </div>
                </>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        // ugcData: state.moderation.ugcData,
    }
}
const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChooseMethod);
